.wrapper{
    width: 50%;
    margin: 0 auto;
    background-color: lightseagreen;
    border: 2px solid black;
    border-radius: 10px;
}
.posts{
    margin: 10px;
}
.text{
    margin: 10px;
    border-radius: 5px;
    border: 2px solid red;
}
.container{
    display: flex;
}
.button_wrapper{
    margin-top: 15px;
}
.button{
    border: 2px solid black;
    width: 50px;
    height: 30px;
    background-color: red;
    color: white;
    border-radius: 10px;
}